import {__} from '@wordpress/i18n';

import {
    PanelBody,
    SelectControl,
    RangeControl,
    TextControl,
    Disabled
} from '@wordpress/components';

import {
    InspectorControls,
    useBlockProps
} from '@wordpress/block-editor';

import ServerSideRender from '@wordpress/server-side-render';

export default function Edit(props) {

    const {attributes, setAttributes} = props;

    const {
        heading,
        heading_tag,
        total,
        layout,
        ids,
        where
    } = attributes;

    const setAttribute = (name, value) => {
        setAttributes({[name]: value});
    };

    const layoutOptions = [
        {label: __('Layout 1'), value: '1'},
        {label: __('Layout 2'), value: '2'},
        {label: __('Layout 3'), value: '3'},
        {label: __('Layout 4'), value: '4'},
        {label: __('Layout 5'), value: '5'},
        {label: __('Layout 6'), value: '6'}
    ];

    const whereOptions = [
        {label: __('Same Tags and Categories'), value: 'all'},
        {label: __('Categories'), value: 'category'},
        {label: __('Tags'), value: 'tag'},
    ];

    const headingTagOptions = [
        {label: __('H2'), value: 'h2'},
        {label: __('H3'), value: 'h3'},
        {label: __('H4'), value: 'h4'},
        {label: __('H5'), value: 'h5'},
        {label: __('H6'), value: 'h6'},
        {label: __('p'), value: 'p'},
        {label: __('Div'), value: 'div'},
    ];

    let wrapperClassName = 'gb-wrap rb-gutenberg-related';

    const blockProps = useBlockProps({
        className: wrapperClassName,
    });

    return (
        <div {...blockProps}>
            <InspectorControls>
                <PanelBody title={__('General')} initialOpen={true}>
                    <SelectControl
                        label={__('Layout')}
                        value={layout}
                        options={layoutOptions}
                        onChange={(value) => setAttribute('layout', value)}
                    />
                    <TextControl
                        value={heading}
                        label={__('Heading')}
                        onChange={(value) => setAttribute('heading', value)}
                    />
                    <SelectControl
                        label={__('Heading Tag')}
                        value={heading_tag}
                        options={headingTagOptions}
                        onChange={(value) => setAttribute('heading_tag', value)}
                    />
                </PanelBody>
                <PanelBody title={__('Query Filters')} initialOpen={true}>
                    <SelectControl
                        label={__('Where get Posts?')}
                        value={where}
                        options={whereOptions}
                        onChange={(value) => setAttribute('where', value)}
                    />
                    <TextControl
                        label={__('or Custom IDs?')}
                        help={__('Input post IDs, separated by commas (,)')}
                        value={ids}
                        options={whereOptions}
                        onChange={(value) => setAttribute('ids', value)}
                    />
                    <RangeControl
                        label={__('Total')}
                        value={total}
                        onChange={(value) => setAttribute('total', value)}
                        step={1}
                        max={30}
                        min={0}
                    />
                </PanelBody>
            </InspectorControls>
            <Disabled>
                <ServerSideRender
                    block="foxiz/related"
                    attributes={attributes}
                />
            </Disabled>
        </div>
    )
}
